import React, { useContext } from 'react'
import { Link, useParams } from 'react-router-dom'
import { UserContext } from '../untils/context'

function User() {
  //  ici on devrait pas avoir const { id } = useParams(). L'id serait plutt rcuprer depuis le UserContext, c'est--dire les donnes renvoyes par le backend.

  const { userId } = useContext(UserContext)
  return (
    <React.Fragment>
      <Link
        to={`/dashboard/${userId && userId.userId}`}
        className="text-white text-decoration-none col"
      >
        Dashboard
      </Link>
    </React.Fragment>
  )
}

export default User
